import React, { Component } from 'react';
import "./zodiac.less";
import AriesPng from 'assets/img/list/aries.png';
import TaurusPng from 'assets/img/list/taurus.png';
import GeminiPng from 'assets/img/list/gemini.png';
import CancerPng from 'assets/img/list/cancer.png';
import LeoPng from 'assets/img/list/leo.png';
import VirgoPng from 'assets/img/list/virgo.png';
import LibraPng from 'assets/img/list/libra.png';
import ScorpioPng from 'assets/img/list/scorpio.png';
import SagittariusPng from 'assets/img/list/sagittarius.png';
import CapricornPng from 'assets/img/list/capricorn.png';
import AquariusPng from 'assets/img/list/aquarius.png';
import PiscesPng from 'assets/img/list/pisces.png';

import BackgroundPng from 'assets/img/bg.jpg';

import Calendar from 'components/Calendar/Calendar';
import { connect } from 'react-redux';
import { getLoadingShow } from 'store/module/loading';

class ZodiacComponent extends Component {
    constructor(props) {
        super(props)
        this.state = {
            key: ""
        }
        window.changePage = this.changePage
   }

   handleClick = (key) => {
       this.setState({
           key
       })
       this.props.toggleLoading(true)
   }

   handleCalendarEnter = (key) => {
        if(this.props.closeCalendar) {
            this.props.closeCalendar()
        }
        this.setState({
            key
        })
        this.props.toggleLoading(true)
    }

    changePage = () => {
        this.props.history.push({pathname:"/main/zodiacdesc", key: this.state.key});
    }

   render() {
       const { showCalendar, closeCalendar } = this.props;

       return (
            <div className="zodiac-root">
                <img className="background" src={BackgroundPng} alt="" />
                <img className="sagittarius" src={SagittariusPng} onClick={()=>this.handleClick("sagittarius")} alt="" />
                <img className="pisces" src={PiscesPng} onClick={()=>this.handleClick("pisces")} alt="" />
                <img className="aquarius" src={AquariusPng} onClick={()=>this.handleClick("aquarius")} alt="" />
                <img className="taurus" src={TaurusPng} onClick={()=>this.handleClick("taurus")} alt="" />
                <img className="libra" src={LibraPng} onClick={()=>this.handleClick("libra")} alt="" />
                <img className="scorpio" src={ScorpioPng} onClick={()=>this.handleClick("scorpio")} alt="" />
                <img className="cancer" src={CancerPng} onClick={()=>this.handleClick("cancer")} alt="" />
                <img className="aries" src={AriesPng} onClick={()=>this.handleClick("aries")} alt="" />
                <img className="virgo" src={VirgoPng} onClick={()=>this.handleClick("virgo")} alt="" />
                <img className="leo" src={LeoPng} onClick={()=>this.handleClick("leo")} alt="" />
                <img className="gemini" src={GeminiPng} onClick={()=>this.handleClick("gemini")} alt="" />
                <img className="capricorn" src={CapricornPng} onClick={()=>this.handleClick("capricorn")} alt="" />
                { showCalendar && <Calendar closeCalendar={closeCalendar} onEnter={this.handleCalendarEnter} />}
            </div>
       )
   }
}

export default connect(state => ({
    loading: getLoadingShow(state)
  }),
  dispatch => ({
      toggleLoading: (show) => dispatch({ type: "LOADING_SHOW", doc: { show } })
  })
)(ZodiacComponent);